<template>
  <v-container>
      
    <v-row>
      <v-col
        three-line
        v-for="(body, i) in case_study.body"
        v-bind:key="i"
        cols="12"
      >
        <v-row>
          <v-col cols="12">
            <v-card color="purple darken-4" dark>
              <div class="d-flex flex-no-wrap justify-space-between">
                <v-card-title class="headline">{{
                  "بخش " + (i + 1)
                }}</v-card-title>
              </div>
            </v-card>
            <v-card color="teal lighten-5">
              <div class="d-flex flex-no-wrap justify-space-between">
                <v-card-title v-html="body" class="headline"> </v-card-title>
              </div>
            </v-card>
          </v-col>
          <v-col
            class="questions"
            v-for="(q, j) in case_study.questions[i]"
            v-bind:key="j"
            cols="12"
          >
            <v-card color="blue darken-4" dark>
              <div class="d-flex flex-no-wrap justify-space-between">
                <v-card-title class="headline"
                  >{{ j + 1 }} <br /><span v-html="q[0]"></span
                ></v-card-title>
              </div>
            </v-card>
            <v-card color="light-green lighten-5">
              <div class="d-flex flex-no-wrap justify-space-between">
                <v-card-title v-html="q[1]" class="headline"></v-card-title>
              </div>
            </v-card>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import axios from "axios";

export default {
  name: "view-case",
  data() {
    return {
      case_study: [],
    };
  },
  mounted() {
    axios
      .post("http://casestudy123.atwebpages.com/rest.php", {
        action: "showOne",
        id: this.$route.params.case_id,
      })
      .then((res) => {
        this.case_study = res.data;
        console.log(this.case_study.others);
        this.case_study.questions = JSON.parse(this.case_study.questions);
        this.case_study.body = JSON.parse(this.case_study.body);
        this.case_study.others = JSON.parse(this.case_study.others);
      });
  },
};
</script>

<style>
.headline {
  word-break: break-word;
}
</style>