<template>
  <div class="home">
    <v-container>
      <v-row dense>
        <v-col v-for="(item, i) in items" :key="i" cols="12">
          <v-card color="#952175" dark>
            <div class="d-flex flex-no-wrap justify-space-between">
              <div>
                <v-card-title
                  class="headline"
                  v-text="item.title"
                ></v-card-title>

                <v-card-subtitle v-text="text(item.body)"></v-card-subtitle>

                <router-link
                  v-bind:to="{ name: 'view-case', params: { case_id: item.id } }"
                >
                  <v-card-actions>
                    <v-btn large color="primary" elevation="2">
                      مطالعه مورد
                    </v-btn>
                  </v-card-actions>
                </router-link>
              </div>

              <v-avatar class="ma-3" size="125" tile>
                <v-img src="case_study.png"></v-img>
              </v-avatar>
            </div>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
// @ is an alias to /src
//import HelloWorld from '@/components/HelloWorld.vue'
import axios from "axios";

export default {
  name: "Cases",
  data() {
    return {
      items: [
        {
          color: "#1F7087",
          src: "https://cdn.vuetifyjs.com/images/cards/foster.jpg",
          title: "Supermodel",
          artist: "Foster the People",
        },
        {
          color: "#952175",
          src: "https://cdn.vuetifyjs.com/images/cards/halcyon.png",
          title: "Halcyon Days",
          artist: "Ellie Goulding",
        },
      ],
    };
  },
  mounted() {
    axios
      .post("http://casestudy123.atwebpages.com/rest.php", {
        action: "showTitle",
      })
      .then((res) => {
        console.log(res.data);
        this.items = res.data.cases;
      });
  },
  methods: {
    strip_html_tags(str = "") {
      if (str === null || str === "") return false;
      else str = str.toString();
      return str.replace(/<[^>]*>/g, "");
    },
    text(txt) {
      let newText = "";
      newText = this.strip_html_tags(txt);
      return newText.toString().substr(0, 150) + " ...";
    },
  },
  components: {},
};
</script>
